<template>
    <div class="login">
        <form class="wrapper">
            <i @click.stop="close" class="iconfont icon-2guanbi"></i>
            <div>
                <label for="name">手机号</label>
                <input autofocus id="name" type="number" v-model="user">
            </div>
            <div>
                <label for="">密码</label>
                <input type="password" v-model="password">  
            </div>
            <div>
                <button class="botton" @click.prevent.stop="toLogin(user,password)">登录</button>
            </div>
        </form>
    </div>
</template>

<script>
    import { login } from '@/api/user'
    export default {
        data(){
            return {
                user: '',
                password: ''
            }
        },
        props: {

        },
        computed:{

        },
        methods:{
            toLogin(user,password){
                // this.$router.push("/user")
                this.user = ''
                this.password = ''
                login(user,password).then(res => {
                    if (res.data.code === 200) {
                        // console.log(res.data);
                        this.$router.push({name:"user",params: res.data})
                    }
                })
                this.$emit('close')
            },
            close(){
                this.$emit('close')
            }
        },
        created(){

        },
        components:{

        },
    }
</script>
<style lang="less" scoped>
    @import "../../common/style/variable.less";
    .login {
            position: absolute;
            height: 210px;
            width: 300px;
            left: 50%;
            top: 50%;
            margin-left: -150px;
            margin-top: -100px;
            z-index: 100;
            background: @color-background;
            border-radius: 10px;
            color: #000;
            border: 1px solid rgb(206, 198, 198);
            padding: 10px 20px;
            box-sizing: border-box;
            .wrapper {
                width: 100%;
                height: 100%;
                position: relative;
                div {
                    display: flex;
                    flex-wrap: wrap;
                    margin: 10px;
                    label {
                        width: 100%;
                    }
                    input {
                        width: 100%;
                        height: 30px;
                        line-height: 30px;
                        margin-top: 10px;
                        border: 1px solid rgb(173, 165, 165);
                        outline: none;
                        border-radius: 5px;
                        padding-left: 10px;
                    }
                    button {
                        outline: none;
                        width: 120px;
                        height: 40px;
                        line-height: 38px;
                        border: 1px solid #dedede;
                        background: @color-theme;
                        color: #fff;
                        font-size: 16px;
                        text-align: center;
                        border-radius: 10px;
                        margin: 0 auto;
                    }
                }
                .iconfont {
                    position: absolute;
                    right: -10px;
                    top: -10px;
                    font-size: @font-size-xxxl;
                }
            }
        }
</style>