import Vue from 'vue'
import Router from 'vue-router'

const Recommend = () => import("../views/recommend.vue") 
const Search = () => import("../views/search.vue") 
const Hot = () => import("../views/hot/hot.vue") 
const Playlist = () => import("../components/playlist/playlist.vue")
const Song = () => import("../views/song/song.vue")
const User = () => import("../views/user/user.vue")


Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/recommend'
        },
        {
            path: '/recommend',
            name: 'recommend',
            component: Recommend
            // children: [
            //     {
            //         path: ':id',
            //         component: Playlist
            //     }
            // ]
        },
        {
            path: '/hot',
            name: 'hot',
            component: Hot
        },
        {
            path: '/search',
            name: 'search',
            component: Search
        },
        {
            path: '/playlist',
            name: 'playlist',
            component: Playlist
        },
        {
            path: '/song',
            name: 'song',
            component: Song
        },
        {
            path: '/user',
            name: 'user',
            component: User
        }
    ]
})
