<template>
    <div id="app">
        <my-header @show="(s) => {loginShow = s}" v-show="flag"></my-header>
        <tab v-show="flag"></tab>
        <login v-show="loginShow" @close="(() => {loginShow = false})" ></login>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
        
    </div>
</template>
<script>
    import myHeader from './components/header/Header';
    import Tab from './components/tab/Tab'
    import Login from './components/login/login'
    export default {
        data () {
            return {
                loginShow: false      
            }
        },
        components: {
            myHeader,
            Tab,
            Login
        },
        computed: {
            flag(){
                return (this.$route.path !== '/playlist') && (this.$route.path !=='/song') && (this.$route.path !== '/user')
            }
        }
    }
</script>

